// VidIntro  JS - VidIntro.js

import $ from 'jquery';
import "../css/vidintro.css";
import vidintro_data from '../data/vidintro';


//    /////////
function jqFn(){
    $(()=>{

    }); //////// jQB ///////////
} ////////////// jQFn ///////////

function VidIntro(props){ 
    // props.pg -  
    // props.mm -  CSS"on"

    //  
    const sdt = vidintro_data[props.pg];

    //    : desc  / sum  
    const lcode = (data) => { // data desc/sum  
        return(
            <>
                {data.split('*')[0]}
                <a href={sdt.link[1]} target='_blank'>
                    {sdt.link[0]}
                </a>
                {data.split('*')[1]}            
            </>
        )
    }

    return(
        <>
        {/*  */}
        <section className={'vidbox'+' '+props.mm}>
            {/*  */}
            <div className='vb1'>
            <iframe src={sdt.vsrc} title={sdt.btit}></iframe>
            </div>
            {/*  */}
            <div className='vb2'>
                <h3>{sdt.stit}</h3>
                <h2>{sdt.btit}</h2>
                <p>
                    {/* (*)  
                    indexOf() ->  -1 */}
                    {
                        sdt.sum.indexOf('*') == -1 ?
                        sdt.sum : lcode(sdt.sum)
                    }
                </p>
                <p className='desc'>
                    {/* (*)  
                    indexOf() ->  -1 */}
                    {
                        sdt.desc.indexOf('*') == -1 ?
                        sdt.desc : lcode(sdt.desc)
                    }


                    
                    

                </p>
                {/*   */}
            </div>
        </section>
        {/*   JS */}
        {jqFn()}
        </>
    )
}

export default VidIntro;