///    - MenuBtn.js
import $ from "jquery";
import "../css/menubtn.css";
import menubtn_data from "../data/menubtn";
import {Link} from "react-router-dom";

//    /////////
function jqFn() {
    $(() => {


    }); //////// jQB ///////////
} ////////////// jQFn ///////////

function MenuBtn() {
    return (
        <>
            <section className="menubtn">
            {
                menubtn_data.map((v,i)=>
                    <div key={i}>
                        <div className="imbx">
                            <img src={v.isrc} alt="" />
                        </div>
                        <div className="titbx">
                            <h3>{v.tit.split('^')[0]}</h3>
                            <h2>{v.tit.split('^')[1].toUpperCase()}</h2>
                        </div>
                        <div className="btnbx">
                            {/*     Link ! */}
                            <Link to={v.link}>
                                <button>
                                    {v.btn.toUpperCase()}
                                </button>
                            </Link>
                        </div>
                    </div>
                )
                }
            </section>
            {/*   JS */}
            {jqFn()}
        </>
    );
}

export default MenuBtn;
