import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
/*  */
import $ from 'jquery';

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";

import "./swipervid.css";

// import required modules
import { Navigation } from "swiper";
//  
import swipervid_data from "../data/swipervid";

export default function SwiperVid(props) {
    //  
    const sdt = swipervid_data;

    //  
    const showVid = (src,tit) => { 
        // src-, tit-
        console.log(src,tit);

        let ifr = $(".playvid iframe");
        // 1.  src
        ifr.attr("src",src+"?autoplay=1");
        // 2.   
        $(".ifrtit").text(tit); 

        let vbx = $(".vidbx");
        // 3.   
        vbx.fadeIn(300);
        // 4.  
        $(".cbtn").click(()=>{
            vbx.fadeOut(300);
            ifr.attr("src","");
        });
    }; //////////// showVid ///////////////////

    return (
        <>
            <Swiper
                slidesPerView={4}
                spaceBetween={20}
                navigation={true}
                modules={[Navigation]}
                className="mySwiper">
                {sdt.map((v, i) => (
                    <SwiperSlide key={i}>
                        <section className="swinbx" 
                        onClick={()=>showVid(v.vsrc,v.tit)}>
                            {/*  */}
                            <div className="vidimg">
                                <img src={v.isrc} alt={v.tit}></img>
                            </div>
                            {/*  */}
                            <div className="vidtit">
                                <h4>{v.cat}</h4>
                                <h3>{v.tit}</h3>
                            </div>
                        </section>
                    </SwiperSlide>
                ))}
            </Swiper>
        </>
    );
}
